import React, {useState} from 'react'

import Search from './Search';
import Product from './Product';

function ProductList({products}) {

    const [search, setSearch] = useState("");

    const wrapperStyle = {
        border:'3px solid red',
        width:'200px',
        height:'800px',
        overflowY:'scroll'
    }

    const productList = () => 
        products.map(
            (product) => 
                ((search===null || product.search_key.includes(search)) 
                && product.trading_disabled===false 
                && product.quote_currency!=='EUR')?
                    (<Product key={product.id} product={product}/>):null)

    return (
        <div style={wrapperStyle}>
            <Search search={search} setSearch={val => setSearch(val)}/>
            {(products)?productList():'Loading...'}
        </div>
    )
}

export default ProductList;